      <th>–ò–º—è</th>
      <th>–û—Ç—á–µ—Å—Ç–≤–æ</th>
      <th>–í–æ–∑—Ä–∞—Å—Ç</th>
      <th>–ú–µ–¥.–∫–Ω–∏–∂–∫–∞</th>
      <th>–ì–æ—Ä–æ–¥</th>
      <th>–¢–µ–ª–µ—Ñ–æ–Ω</th>
